  color: black
}

.playercomponentcontainer {
        background-color: gainsboro;
}
.hide {
        display: none
}
.playbutton {
        background-color: cadetblue;
        margin-left: 10px;
        position: relative;
        float: left;
}
.mutebutton {
        background-color: chocolate;
        float: left;
}
.unmutebutton {
        background-color: cornsilk;
        float: left;
}
.lolbutton {
        float: left;
        font-size: 80px;
        color: darkgreen;
        background-color: orange;
        margin: 10px
}
.editbutton{
        background-color: cadetblue;
}
.nowplayingdiv {
        float: left;
        font-size: 14px;
        margin-left: 10px;
}
.nowplayingtitle {
        float: left;
        font-size: 18px;
        margin-left: 10px;
}
.hideondesktop {
        display: none;
}
.commentsoutputbox {
        background-color: cadetblue;
        margin-left: 60px;
        margin-top: 20px;
        margin-right: 60px;
        padding: 7px;
        border-style: solid;
        border-width: 2px;
}
.commentitem {
        list-style: none;
        text-align: left;
        border-style: ridge;
        border-width: 2px;
        background-color: whitesmoke;
        margin-top: 5px;
}
.commentfield {
        width: 80%;
        background-color: whitesmoke;
}
@media (max-width: 500px) {
.playbutton {
        font-size: 12px;
}
